 .BuildControls_OrderButton__yuGrW {
    background-color: #DAD735;
    outline: none;
    cursor: pointer;
    border: 1px solid #966909;
    color: #966909;
    font-family: inherit;
    font-size: 1.2em;
    padding: 15px 30px;
    box-shadow: 2px 2px 2px #966909;
}

.BuildControls_OrderButton__yuGrW:hover, .BuildControls_OrderButton__yuGrW:active {
    background-color: #A0DB41;
    border: 1px solid #966909;
    color: #966909;
}

.BuildControls_OrderButton__yuGrW:disabled {
    background-color: #C7C6C6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888888;
}

.BuildControls_OrderButton__yuGrW:not(:disabled) {
    -webkit-animation: BuildControls_enable__2_pX0 0.3s linear;
            animation: BuildControls_enable__2_pX0 0.3s linear;
}

@-webkit-keyframes BuildControls_enable__2_pX0 {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes BuildControls_enable__2_pX0 {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
